'use client';

import React, { useState, useEffect } from 'react';
import { Leaf, Droplets, Recycle, Waves, Sun, Wrench, DollarSign, Flower2, Fish, ArrowRightLeft, Sparkles, Heart } from 'lucide-react';

type FeatureCardProps = {
  icon: React.ComponentType<React.SVGProps<SVGSVGElement>>;
  title: string;
  children: React.ReactNode;
  id: string;
  gradient: string;
  delay?: number;
};

type FloatingElementProps = {
  children: React.ReactNode;
  delay?: number;
};

export default function PrincipesPage() {
  const [scrollY, setScrollY] = useState(0);
  const [visibleSections, setVisibleSections] = useState(new Set());
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
    const handleScroll = () => setScrollY(window.scrollY);
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  useEffect(() => {
    if (!isClient) return;
    
    // Petit délai pour s'assurer que le DOM est prêt
    const timer = setTimeout(() => {
      const observer = new IntersectionObserver(
        (entries) => {
          entries.forEach((entry) => {
            if (entry.isIntersecting) {
              setVisibleSections(prev => new Set([...prev, entry.target.id]));
            }
          });
        },
        { threshold: 0.1 }
      );

      document.querySelectorAll('[data-section]').forEach((el) => {
        observer.observe(el);
      });

      // Cleanup function
      return () => observer.disconnect();
    }, 100);

    return () => clearTimeout(timer);
  }, [isClient]);

  const FeatureCard = ({ icon: Icon, title, children, id, gradient, delay = 0 }) => {
    const isVisible = visibleSections.has(id);
    
    return (
      <div
        id={id}
        data-section
        className={`relative overflow-hidden rounded-3xl p-8 backdrop-blur-sm border border-white/20 transition-all duration-1000 hover:scale-105 hover:shadow-2xl group ${
          isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'
        }`}
        style={{
          background: `linear-gradient(135deg, ${gradient})`,
          transitionDelay: `${delay}ms`
        }}
      >
        <div className="absolute inset-0 bg-gradient-to-br from-white/10 to-transparent"></div>
        <div className="absolute top-0 right-0 w-32 h-32 bg-white/5 rounded-full -translate-y-16 translate-x-16 group-hover:scale-150 transition-transform duration-700"></div>
        
        <div className="relative z-10">
          <div className="flex items-center gap-4 mb-6">
            <div className="p-3 rounded-2xl bg-white/20 backdrop-blur-sm group-hover:rotate-12 transition-transform duration-500">
              <Icon className="h-8 w-8 text-white" />
            </div>
            <h2 className="text-2xl font-bold text-white">{title}</h2>
          </div>
          <div className="text-white/90 leading-relaxed space-y-4">
            {children}
          </div>
        </div>
        
        <div className="absolute bottom-0 left-0 w-full h-1 bg-gradient-to-r from-white/20 to-white/40 group-hover:h-2 transition-all duration-500"></div>
      </div>
    );
  };

  const FloatingElement = ({ children, delay = 0 }) => (
    <div
      className="animate-pulse"
      style={{
        animation: `float 6s ease-in-out infinite`,
        animationDelay: `${delay}s`
      }}
    >
      {children}
    </div>
  );

  return (
    <main className="relative min-h-screen overflow-hidden">
      {/* Fond animé avec dégradés */}
      <div className="fixed inset-0 z-0">
        <div className="absolute inset-0 bg-gradient-to-br from-emerald-50 via-blue-50 to-teal-50"></div>
        <div 
          className="absolute inset-0 opacity-30"
          style={{
            background: `radial-gradient(circle at ${50 + scrollY * 0.02}% ${50 + scrollY * 0.01}%, rgba(34, 197, 94, 0.3) 0%, transparent 50%)`,
          }}
        ></div>
        {/* <div className="absolute inset-0 bg-[url('data:image/svg+xml,%3Csvg width="20" height="20" xmlns="http://www.w3.org/2000/svg"%3E%3Cdefs%3E%3Cpattern id="grid" width="20" height="20" patternUnits="userSpaceOnUse"%3E%3Cpath d="M 20 0 L 0 0 0 20" fill="none" stroke="rgb(34 197 94 / 0.1)" stroke-width="1"/%3E%3C/pattern%3E%3C/defs%3E%3Crect width="100%25" height="100%25" fill="url(%23grid)" /%3E%3C/svg%3E')] opacity-20"></div> */}
      </div>

      {/* Éléments flottants décoratifs */}
      {isClient && (
        <div className="fixed inset-0 pointer-events-none z-10">
          <FloatingElement delay={0}>
            <div className="absolute top-20 left-10 text-emerald-300/20">
              <Droplets className="h-12 w-12" />
            </div>
          </FloatingElement>
          <FloatingElement delay={2}>
            <div className="absolute top-40 right-20 text-blue-300/20">
              <Waves className="h-16 w-16" />
            </div>
          </FloatingElement>
          <FloatingElement delay={4}>
            <div className="absolute bottom-40 left-20 text-green-300/20">
              <Leaf className="h-14 w-14" />
            </div>
          </FloatingElement>
        </div>
      )}

      <div className="relative z-20">
        {/* Hero Section */}
        <section className="py-32 px-4 sm:px-6 lg:px-8">
          <div className="max-w-6xl mx-auto text-center">
            <div className="inline-flex items-center gap-2 px-6 py-3 rounded-full bg-emerald-100/80 backdrop-blur-sm border border-emerald-200/50 text-emerald-800 font-medium mb-8 animate-fade-in">
              <Sparkles className="h-5 w-5" />
              <span>100% Naturel • 0% Chimique</span>
            </div>
            
            <h1 className="text-5xl md:text-7xl font-bold bg-gradient-to-r from-emerald-600 via-blue-600 to-teal-600 bg-clip-text text-transparent mb-8 animate-fade-in-up">
              Piscines Naturelles
              <br />
              <span className="text-4xl md:text-6xl">« Organiques »</span>
            </h1>
            
            <p className="text-xl md:text-2xl text-gray-700 max-w-4xl mx-auto leading-relaxed mb-12 animate-fade-in-up" style={{ animationDelay: '200ms' }}>
              Découvrez l'alternative écologique qui transforme votre jardin en écosystème vivant. 
              Une eau cristalline, sans produits chimiques, en harmonie avec la nature.
            </p>

            <div className="flex flex-wrap justify-center gap-4 animate-fade-in-up" style={{ animationDelay: '400ms' }}>
              <div className="flex items-center gap-2 px-6 py-3 rounded-full bg-white/80 backdrop-blur-sm border border-gray-200/50 text-gray-700">
                <Heart className="h-5 w-5 text-red-500" />
                <span>Écologique</span>
              </div>
              <div className="flex items-center gap-2 px-6 py-3 rounded-full bg-white/80 backdrop-blur-sm border border-gray-200/50 text-gray-700">
                <Fish className="h-5 w-5 text-blue-500" />
                <span>Écosystème vivant</span>
              </div>
              <div className="flex items-center gap-2 px-6 py-3 rounded-full bg-white/80 backdrop-blur-sm border border-gray-200/50 text-gray-700">
                <Sun className="h-5 w-5 text-yellow-500" />
                <span>Énergie solaire</span>
              </div>
            </div>
          </div>
        </section>

        {/* Sections avec cartes */}
        <section className="py-20 px-4 sm:px-6 lg:px-8">
          <div className="max-w-7xl mx-auto space-y-12">
            
            <FeatureCard
              icon={Leaf}
              title="Qu'est-ce qu'une piscine organique ?"
              id="definition"
              gradient="rgba(34, 197, 94, 0.8), rgba(16, 185, 129, 0.6)"
              delay={0}
            >
              <p>
                Les piscines naturelles « organiques » sont une alternative écologique aux piscines traditionnelles. 
                Elles fonctionnent grâce à un équilibre naturel entre la zone de baignade et une zone de filtration 
                composée de plantes aquatiques, sans aucun produit chimique.
              </p>
            </FeatureCard>

            <div className="grid lg:grid-cols-2 gap-8">
              <FeatureCard
                icon={Droplets}
                title="La filtration de l'eau"
                id="filtration"
                gradient="rgba(59, 130, 246, 0.8), rgba(147, 197, 253, 0.6)"
                delay={100}
              >
                <p>Une piscine naturelle se divise en deux zones de surface équivalente :</p>
                <ul className="space-y-2 mt-4">
                  <li className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-white/60"></div>
                    La zone de baignade
                  </li>
                  <li className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-white/60"></div>
                    La zone de filtration
                  </li>
                </ul>
                <p className="mt-4">
                  La zone de filtration agit comme un gigantesque filtre biologique composé de graviers roulés et de plantes 
                  (iris, papyrus, nénuphars, lotus…). Ces plantes consomment les nutriments et empêchent la prolifération d'algues.
                </p>
              </FeatureCard>

              <FeatureCard
                icon={Recycle}
                title="Comment cela fonctionne ?"
                id="fonctionnement"
                gradient="rgba(16, 185, 129, 0.8), rgba(52, 211, 153, 0.6)"
                delay={200}
              >
                <p>Pour rester propre, une piscine naturelle doit répondre à deux besoins fondamentaux :</p>
                <ul className="space-y-2 mt-4">
                  <li className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-white/60"></div>
                    La circulation de l'eau
                  </li>
                  <li className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-white/60"></div>
                    La filtration biologique
                  </li>
                </ul>
              </FeatureCard>
            </div>

            <FeatureCard
              icon={Flower2}
              title="Les avantages d'une piscine organique"
              id="avantages"
              gradient="rgba(236, 72, 153, 0.8), rgba(251, 113, 133, 0.6)"
              delay={300}
            >
              <div className="grid md:grid-cols-2 gap-6">
                <ul className="space-y-3">
                  <li className="flex items-center gap-3">
                    <div className="w-3 h-3 rounded-full bg-white/60"></div>
                    0 produit chimique (ni chlore, ni sel)
                  </li>
                  <li className="flex items-center gap-3">
                    <div className="w-3 h-3 rounded-full bg-white/60"></div>
                    Un écosystème vivant : plantes, fleurs, insectes
                  </li>
                </ul>
                <ul className="space-y-3">
                  <li className="flex items-center gap-3">
                    <div className="w-3 h-3 rounded-full bg-white/60"></div>
                    Eau claire et potable grâce aux plantes filtrantes
                  </li>
                  <li className="flex items-center gap-3">
                    <div className="w-3 h-3 rounded-full bg-white/60"></div>
                    Pas de moustiques : la faune régule les larves
                  </li>
                </ul>
              </div>
            </FeatureCard>

            <FeatureCard
              icon={ArrowRightLeft}
              title="La circulation de l'eau"
              id="circulation"
              gradient="rgba(99, 102, 241, 0.8), rgba(129, 140, 248, 0.6)"
              delay={400}
            >
              <p>
                Contrairement aux piscines traditionnelles (pompes à eau énergivores et fragiles), les piscines organiques utilisent des <strong>pompes à air</strong>.
                Ces pompes propulsent de l'air en profondeur, créant un courant doux qui traverse le filtre biologique.
              </p>
              <div className="mt-4 p-4 rounded-xl bg-white/20 backdrop-blur-sm">
                <p className="font-medium">Consommation : ~50W • Compatible solaire • Sans danger pour la faune</p>
              </div>
            </FeatureCard>

            <FeatureCard
              icon={DollarSign}
              title="Quel est le coût ?"
              id="cout"
              gradient="rgba(245, 158, 11, 0.8), rgba(251, 191, 36, 0.6)"
              delay={500}
            >
              <div className="grid md:grid-cols-2 gap-8">
                <div>
                  <h3 className="text-xl font-bold text-white mb-4">Construction</h3>
                  <p className="mb-4">Inférieur à une piscine traditionnelle. La principale dépense est le liner :</p>
                  <ul className="space-y-2 text-sm">
                    <li>• Creuser et préparer le terrain</li>
                    <li>• Poser géotextile + liner + géotextile</li>
                    <li>• Installer système PVC et pompe à air</li>
                    <li>• Déposer graviers et plantes aquatiques</li>
                  </ul>
                </div>
                <div>
                  <h3 className="text-xl font-bold text-white mb-4">Fonctionnement</h3>
                  <div className="p-4 rounded-xl bg-white/20 backdrop-blur-sm">
                    <p className="font-bold text-2xl text-white mb-2">≈ 0 €</p>
                    <p className="text-sm">
                      Pompe à air solaire, filtration naturelle par les plantes. 
                      Entretien léger à l'automne seulement.
                    </p>
                  </div>
                </div>
              </div>
            </FeatureCard>

          </div>
        </section>

        {/* CTA Final */}
        <section className="py-20 px-4 sm:px-6 lg:px-8">
          <div className="max-w-4xl mx-auto text-center">
            <div className="p-12 rounded-3xl bg-gradient-to-r from-emerald-500/20 to-blue-500/20 backdrop-blur-sm border border-white/30">
              <h2 className="text-3xl md:text-4xl font-bold text-gray-800 mb-6">
                Prêt à transformer votre jardin ?
              </h2>
              <p className="text-xl text-gray-600 mb-8">
                Créez votre propre écosystème aquatique et profitez d'une baignade 100% naturelle.
              </p>
              <button className="inline-flex items-center gap-2 px-8 py-4 rounded-full bg-gradient-to-r from-emerald-600 to-blue-600 text-white font-semibold hover:shadow-lg hover:scale-105 transition-all duration-300">
                <Waves className="h-5 w-5" />
                Découvrir nos réalisations
              </button>
            </div>
          </div>
        </section>
      </div>

      <style jsx>{`
        @keyframes float {
          0%, 100% { transform: translateY(0px); }
          50% { transform: translateY(-20px); }
        }
        
        @keyframes fade-in {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        
        @keyframes fade-in-up {
          from { 
            opacity: 0; 
            transform: translateY(30px); 
          }
          to { 
            opacity: 1; 
            transform: translateY(0); 
          }
        }
        
        .animate-fade-in {
          animation: fade-in 1s ease-out;
        }
        
        .animate-fade-in-up {
          animation: fade-in-up 1s ease-out;
        }
      `}</style>
    </main>
  );
}